* {
    font-family: 'Inter', sans-serif;
}

h1, p {
    margin: 0;
}

h1, p {
    margin: 30px;
}

body {
    margin: 0;
    padding: 0;
    position: relative;
    height: 100vh;
    color: #4D5B9E;
    background-color: aliceblue;
}

.bl {
    position: absolute;
    bottom: 0;
}

.tr {
    position: absolute;
    right: 0;
}

.landing-mid-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

.start-quiz {
    font-family: inherit;
    padding: 1em 4em;
    background-color: #4D5B9E;
    color: white;
    border: none;
    border-radius: 10px;
}

.questions, .check-answers {
    width: 70%;
    margin: 0 auto;
}

.check-answers {
    margin-top: 2em;
    text-align: center;
}

.check {
    padding: .8em 3em;
    border-radius: 10px;
    border: none;
    background-color: #4D5B9E;
    color: white;

}

.choose-answer {
    font-family: inherit;
    border: 1px solid #4D5B9E;
    border-radius: 10px;
    margin: .2em 1em 1em 0;
    background-color: aliceblue;
    padding: .3em 2em;
    cursor: pointer;
}

hr {
    border: 0;
    background-color: #D6DBF5;
    height: 1px;
}

.check-answers p {
    margin: .5em;
    font-size: 1.5rem;
    font-weight: bold;
}

@media (max-width: 700px) {
    main {
        text-align: center;
    }
}